import { createRouter, createWebHistory } from 'vue-router'

import Home from '@/pages/Home.vue'


export const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'HomePage', component: Home },
        { path: '/users', name: 'Users', component: () => import('@/pages/Users.vue')},
        { path: '/users/:id', name: 'UserItem', component: () => import('@/pages/UserItem.vue')},
        { path: '/todos', name: 'Todos', component: () => import('@/pages/Products.vue')},
        { path: '/posts', name: 'Posts', component: () => import('@/pages/Posts.vue')},
        { path: '/about', name: 'AboutPage', component: () => import('@/pages/About.vue')},
        { path: '/about/:id', name: 'CarItem', component: () => import('@/pages/CarItem.vue')},
        { path: '/:catchAll(.*)', name: '404', component: () => import('@/pages/NotFound.vue')},
    ]
})